import { Box } from "../../../core/MyBox";
import { MyFormItem } from "../../../core/Input";
import { useState } from "react";
import { MyButton } from "../../../core/MyButton";
import { SettingService } from "../../../../services/taskService";
import { useParams } from "react-router-dom";
import { Fn } from "../../../../helper/function";

export default function ImportFileComponent(props: any) {
    const { setIsLoading } = props;
    const [file, setFile] = useState<string | Blob>();
    const projectKey = useParams().key;
    function handleOnClick() {
        doUpload();
    }

    function doUpload() {
        setIsLoading(true);
        const formData = new FormData();
        if (file) {
            formData.append("file", file);
        }

        SettingService.Import(projectKey, formData)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    Fn.PrintMsgSuccess(rs);
                }
            })
            .finally(() => setIsLoading(false));
    }

    function handleOnClickExport() {
        setIsLoading(true);
        SettingService.Export(projectKey)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    var blob = new Blob([rs.data], { type: "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet" });
                    var url = URL.createObjectURL(blob);
                    window.open(url);
                }
            })
            .finally(() => {
                setIsLoading(false);
            })
    }

    return (
        <Box>
            <MyButton
                type="primary"
                onClick={handleOnClickExport}
                value="Export file" />

            <MyButton
                type="success"
                onClick={handleOnClick}
                value="Import file"
                disabled={file === undefined} />

            <MyFormItem.File
                value={file}
                setValue={setFile} />
        </Box>
    )
}