import { Drawer, Form } from "antd";
import { useCallback, useEffect, useState } from "react";
import Swal from "sweetalert2";
import { Fn } from "../../../../helper/function";
import { SettingService } from "../../../../services/taskService";
import { MyFormItem } from "../../../core/Input";
import { MyButton } from "../../../core/MyButton";

export default function ModifyOtherSettingComponent(props: any) {
    const [value, setValue] = useState('');
    const { isLoading, setIsLoading, projectKey, keyData, visible, setVisible, type, loadData } = props;
    const loadDataDetail = useCallback(() => {
        if (keyData !== undefined) {
            setIsLoading(true);
            SettingService.GetDetailSettingStatus(projectKey, keyData)
                .then((rs: any) => {
                    if (rs.isSuccess) {
                        setValue(rs.data.value);
                    }
                })
                .finally(() => setIsLoading(false));
        }
    }, [projectKey, keyData, setIsLoading]);

    useEffect(() => {
        loadDataDetail();
    }, [loadDataDetail]);

    function onClose() {
        setVisible(false);
    }

    function handleSubmitForm() {
        var body = {
            value: value,
            type: type
        };

        setIsLoading(true);
        if (keyData !== undefined) {
            Edit(body);
        } else {
            Add(body);
        }
    }

    function Add(body: any) {
        SettingService.AddSettingStatus(projectKey, body)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    Fn.PrintMsgSuccessV2(rs);
                    loadData();
                }
            })
            .finally(() => setIsLoading(false));
    }

    function Edit(body: any) {
        SettingService.GetDetailSettingStatus(projectKey, keyData)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    body = Fn.GetTimestampV2(rs, body);
                    SettingService.EditSettingStatus(projectKey, keyData, body)
                        .then((rs: any) => {
                            if (rs.isSuccess) {
                                Fn.PrintMsgSuccessV2(rs);
                                loadData();
                            }
                        })
                        .finally(() => setIsLoading(false));
                } else setIsLoading(false);
            })
    }

    function handleClickDelete() {
        Swal.fire({
            title: "Xác nhận xóa",
            text: 'Bạn có muốn xóa không?',
        }).then((confirm) => {
            if (confirm.isConfirmed) {
                Delete();
            }
        })
    }

    function Delete() {
        setIsLoading(true);
        SettingService.GetDetailSettingStatus(projectKey, keyData)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    SettingService.DeleteSettingStatus(projectKey, keyData, Fn.GetTimestampV2(rs))
                        .then((rs: any) => {
                            if (rs.isSuccess) {
                                Fn.PrintMsgSuccessV2(rs);
                                onClose();
                                loadData();
                            }
                        })
                        .finally(() => setIsLoading(false))
                } else setIsLoading(false);
            })
    }

    return (
        <Drawer
            open={visible}
            onClose={onClose}
            title={keyData !== undefined ? "Chỉnh sửa" : "Thêm mới"}>
            <Form
                layout="vertical"
                onSubmitCapture={handleSubmitForm}>
                <MyFormItem.Input
                    label="Giá trị"
                    value={value}
                    setValue={setValue} />
                <MyButton
                    htmlType="submit"
                    isLoading={isLoading}
                    type="success"
                    value={keyData !== undefined ? "Chỉnh sửa" : "Thêm mới"} />
                <MyButton
                    type="danger"
                    isLoading={isLoading}
                    onClick={handleClickDelete}
                    value="Xóa" />
            </Form>
        </Drawer>
    );
}