import { Drawer, Form } from "antd";
import { useCallback, useEffect, useState } from "react";
import Swal from "sweetalert2";
import { Fn } from "../../../../helper/function";
import { SettingService } from "../../../../services/taskService";
import { MyFormItem } from "../../../core/Input";
import { MyButton } from "../../../core/MyButton";

export default function ModifyStatusSettingComponent(props: any) {
    const [value, setValue] = useState('');
    const [type, setType] = useState('');
    const [isNew, setIsNew] = useState(false);
    const [isClosed, setIsClosed] = useState(false);

    const { title, isLoading, setIsLoading, projectKey, keyData, visible, setVisible, loadData } = props;

    const options = [
        {
            key: "1",
            label: "Trạng thái dự án",
            value: 1
        },
        {
            key: "2",
            label: "Trạng thái sprint",
            value: 2
        },
        {
            key: "3",
            label: "Trạng thái công việc",
            value: 3
        },
        {
            key: "4",
            label: "Trạng thái vấn đề",
            value: 4
        }
    ]

    let loadDataDetail = useCallback(() => {
        if (keyData !== undefined) {
            setIsLoading(true);
            SettingService.GetDetailSettingStatus(projectKey, keyData)
                .then((rs: any) => {
                    if (rs.isSuccess) {
                        const row = rs.data;
                        setValue(row.value);
                        setType(row.type);
                        setIsNew(row.isNew);
                        setIsClosed(row.isClosed);
                    }
                })
                .finally(() => setIsLoading(false));
        }
    }, [keyData, projectKey, setIsLoading]);

    useEffect(() => {
        loadDataDetail();
    }, [loadDataDetail])

    function HandleCloseDrawer() {
        setVisible(false);
    }

    function handleSubmitForm() {
        let body = {
            value: value,
            type: type,
            isNew: isNew,
            isClosed: isClosed
        };

        setIsLoading(true);
        if (keyData === undefined) {
            Add(body);
        } else {
            Edit(body);
        }
    }

    function Add(body: any) {
        SettingService.AddSettingStatus(projectKey, body)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    Fn.PrintMsgSuccessV2(rs);
                    loadData();
                    HandleCloseDrawer();
                }
            })
            .finally(() => { setIsLoading(false) });
    }

    function Edit(body: any) {
        SettingService.GetDetailSettingStatus(projectKey, keyData)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    body = Fn.GetTimestampV2(rs, body);
                    SettingService.EditSettingStatus(projectKey, keyData, body)
                        .then((rs: any) => {
                            if (rs.isSuccess) {
                                Fn.PrintMsgSuccessV2(rs);
                                loadData();
                            }
                        })
                        .finally(() => setIsLoading(false));
                } else setIsLoading(false);
            })
    }

    function HandleClickDelete() {
        Swal.fire({
            title: "Bạn có muốn xóa không",
            text: "Bạn có muốn xóa không",
            showConfirmButton: true,
            showDenyButton: true,
        }).then((result: any) => {
            if (result.isConfirmed) {
                Delete();
            }
        })
    }

    function Delete() {
        setIsLoading(true);
        SettingService.GetDetailSettingStatus(projectKey, keyData)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    SettingService.DeleteSettingStatus(projectKey, keyData, Fn.GetTimestampV2(rs))
                        .then((rs: any) => {
                            if (rs.isSuccess) {
                                Fn.PrintMsgSuccessV2(rs);
                                loadData();
                                HandleCloseDrawer();
                            }
                        })
                }
            })
    }

    return (
        <Drawer
            title={title}
            open={visible}
            onClose={HandleCloseDrawer}>
            <Form
                layout="vertical"
                onSubmitCapture={handleSubmitForm}>
                <MyFormItem.Input
                    label="Giá trị"
                    value={value}
                    setValue={setValue} />

                <MyFormItem.Select
                    label="Loại trạng thái"
                    options={options}
                    value={type}
                    setValue={setType} />

                <MyFormItem.Switch
                    label="Trạng thái mới"
                    value={isNew}
                    setValue={setIsNew} />

                <MyFormItem.Switch
                    label="Trạng thái đóng"
                    value={isClosed}
                    setValue={setIsClosed} />

                <MyButton
                    type="success"
                    htmlType="submit"
                    isLoading={isLoading}
                    value={keyData !== undefined ? "Sửa" : "Thêm"} />

                <MyButton
                    type="danger"
                    value="Xóa"
                    isLoading={isLoading}
                    onClick={HandleClickDelete} />
            </Form>
        </Drawer>
    );
}